<template>
    <div class="page-sidebar shadow-base">
        <a href="" class="sb-brand"><img src="../assets/images/logo_brand.png" alt="InstaPay"></a>
        <div class="sidebar-menu-wrapper">
            <ul class="sidebar-menu">
                <li class="nav-item" id="menu_s1" @click="move('tras')"><a href="javascript:void(0);">거래내역</a></li>
                <li class="nav-item" id="menu_s2"><router-link to="/tras">취소환불</router-link></li>
                <li class="nav-item" id="menu_s3" @click="move('store')"><a href="javascript:void(0);">가맹점 정보</a></li>
                <div class="divider"></div>
                <li class="nav-item" id="menu_s91" v-show="isBookstore">
                    <a href="javascript:void(0);">도서주문</a>
                    <ul class="sub-menu">
                        <li class="nav-item"><a href="javascript:void(0);" @click="move('btras')">책 주문내역</a></li>
                        <li class="nav-item"><a href="javascript:void(0);" @click="move('porder')">출판사 주문</a></li>
                    </ul>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import api from '@/utils/api';

export default {
    name:"PageSidebar",
    data() {
        return {
            //sid:'',
        }
    },
    methods: {
        async setStoreInfo() {
            try {
                let response = await api.getStoreInfo()
                console.log(response.data.status, response.data.result)
                if(response.data.status!=-1) {
                    const sid = response.data.store.sid
                    //localStorage.setItem('instapay_sid', sid)
                    this.$store.dispatch('loadStoreInfo',response.data.store)

                    if(sid == 'l20hz-ip19c-07j15-11o08-g09bu') { // bookstore
                        this.$store.commit('setIfBookstore', true)
                    }
                }
                else {
                    console.log('api request rejected')
                    localStorage.removeItem('instapay_token')
                    localStorage.removeItem('instapay_id')
                    api.updateToken();
                    this.$router.push('/logIn');
                    this.$store.commit('setIfBookstore', false)
                }
            } catch(error) {
                console.log(error)
            }
        },
        move(pathname) {
            // this.$store.commit('setResults', false)
            this.$router.push(`/${pathname}`).catch(()=>{}); // 'Avoided redundant navigation' 에러 방지 
        },
        setCurMenu(curMenu) {
            this.$store.commit('setCurMenu', curMenu)
            this.$store.commit('setResults', false)
            //this.$store.commit('setTrResults', { tras:[]} )
        }
    },
    created() {
/*         if(localStorage.getItem('instapay_id')=='book@instapay.kr' || localStorage.getItem('instapay_id')=='book1@instapay.kr') {
            this.$store.commit('setIfBookstore', true)
        } */
        if(this.sid=='') {
            console.log('sidebar: created', this.sid)
            this.setStoreInfo()
        }

    },
    mounted() {
        console.log('sidebar: mounted', this.currentMenu)
        this.setCurMenu(this.currentMenu)
        //this.$store.commit('setResults', false)
    },
    computed: {
        sid() {
            return this.$store.getters.sid
        },
        currentMenu:function() {
            // return this.$route.path.slice(1)
            return this.$route.matched[0].path.slice(1)
        },
        isBookstore(){
            return this.$store.getters.isBookstore
        }
    },
    watch: {
        currentMenu(newVal) {
            console.log('sidebar: watch, result reset', newVal)
            this.setCurMenu(this.currentMenu)
            // this.$store.commit('setResults', false)
        }
    }
}
</script>
<style scoped>
.page-sidebar { background-color:var(--bg-white);color:var(--text-grey); }
/* .sb-brand { font-size:20px;font-weight:bold; text-align:center; } */
</style>